/* -----------------------------------------------------------------------------
  Root (inc)
----------------------------------------------------------------------------- */
body {
  position: relative;
  margin-top: 77px;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 500;
}

#icon-sprite {
  display: none;
}

.inner-body {
  position: relative;
  width: 100%;
}

main {
  padding-left: 30px;
  padding-right: 15px;
  min-height: calc(100vh - 77px - 60px - 70px);
}

main img {
  max-width: 100%;
  margin-bottom: 1rem;
}

main p {
  font-family: Manrope, sans-serif;
  font-style: normal;
  letter-spacing: 0.03em;
}

main table {
  margin-bottom: 1rem;
}

main table td,
main table th {
  vertical-align: baseline;
}

.sidebar {
  flex-shrink: 0;
}

.sidebar .sidebar-container {
  top: 0;
  overflow-y: auto;
}

a {
  color: #256bd1;
}

a:hover, a:focus {
  color: #3585f9;
}

a:active {
  color: #256bd1;
  font-weight: 700;
}

th p {
  margin: 0;
  font-weight: inherit;
  font-weight: 500;
}

/* -----------------------------------------------------------------------------
  Component: Brand and logo

  Style for the element containing the logo in the header.
----------------------------------------------------------------------------- */
.navbar-brand {
  display: flex;
  justify-content: flex-end;
}

.navbar-brand .logo-link.web {
  padding-bottom: .3rem;
}

.navbar-brand .logo-link.docs {
  margin-left: 5px;
  line-height: 2.15;
  color: #292929;
}

.navbar-brand .logo-link.docs:hover {
  text-decoration: none;
}

/* -----------------------------------------------------------------------------
  Component: Mode switch

  Style for the component that switches the theme style to light or dark mmode.
----------------------------------------------------------------------------- */
#light-dark-switch {
  display: flex;
  flex-direction: row;
  padding-bottom: 8px;
}

#light-dark-switch > span:first-child {
  margin-left: 8px;
}

#light-dark-switch .switch-icon {
  width: 20px;
  height: 20px;
  margin-left: 5px;
  margin-right: 5px;
  display: inline-block;
  background-size: contain;
  background-position: center;
}

#light-dark-switch .switch-icon.icon-sun {
  background: url("../../images/icons/svg-icons.svg#sun");
}

#light-dark-switch .switch-icon.icon-moon {
  background: url("../../images/icons/svg-icons.svg#moon");
}

#mode-switch {
  box-sizing: content-box;
  position: relative;
  top: 1px;
  display: flex;
  flex-direction: row;
  width: 26px;
  height: 16px;
  padding: 0;
  margin: 0 5px;
  background-color: #3585f9;
  border: 1px solid #3585f9;
  border-radius: 25px;
}

#mode-switch.dark {
  justify-content: flex-end;
}

#mode-switch.dark span {
  left: calc(100% - 16px);
}

#mode-switch span {
  position: absolute;
  left: 0;
  display: inline-block;
  width: 16px;
  height: 16px;
  content: "";
  background-color: #fff;
  border-radius: 50%;
  transition: left ease-in .2s;
}

/* -----------------------------------------------------------------------------
  Component: Search box

  Style for the searchbox in general.
----------------------------------------------------------------------------- */
.search-box {
  background-color: #ebf2fb;
  border: 2px solid #c1ccd9;
  border-radius: 10px;
}

.search-box .search-form {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 46px;
}

.search-box .search-form .form-control {
  position: relative;
  z-index: 1;
  width: 100%;
  font-size: 18px;
  font-weight: 400;
  background-color: #ebf2fb;
  border: none;
}

.search-box .search-form .btn-search {
  position: absolute;
  right: 0;
  z-index: 10;
  width: 46px;
  height: 46px;
  padding: 5px;
  background: url("../../images/icons/svg-icons.svg#search");
  background-size: 41px 41px;
  background-position: center;
  border: none;
}

/* -----------------------------------------------------------------------------
  PART: Footer

  Style for the footer under the column containing the element main and the
  right sidebar.
----------------------------------------------------------------------------- */
footer {
  position: absolute;
  right: 0;
  left: 385px;
  height: 70px;
  padding: 15px 15px 15px 30px;
  background: #f6f6f6;
}

footer ul {
  padding-left: 15px;
  margin: 0;
}

footer .nav-link {
  padding: 0 10px;
  color: #292929;
}

footer .nav-link:hover {
  color: #3585f9;
}

body {
  margin-top: 215px;
  background: #F8FAFD;
}

main {
  padding-left: 15px;
  padding-right: 15px;
  min-height: calc(100vh - 215px - 70px);
  padding-top: 60px;
}

main h1 {
  width: 0;
  height: 0;
  overflow: hidden;
}

main .toctree-wrapper ul {
  padding: 0;
  list-style: none;
}

main .toctree-wrapper > ul {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

main .toctree-wrapper > ul .toctree-l1 {
  margin-left: 70px;
  width: calc(33.333% - 70px);
  margin-bottom: 60px;
  position: relative;
  min-height: 133px;
  border-left: 3px solid #256bd1;
}

main .toctree-wrapper > ul .toctree-l1.collapsible .toggle {
  display: inline-block;
  left: -3px;
  font-size: 18px;
  color: #256bd1;
  font-weight: 700;
  padding: 10px 0;
  border-left-color: #F8FAFD;
}

main .toctree-wrapper > ul .toctree-l1.collapsible .toggle::after {
  content: '';
  display: inline-block;
  width: 24px;
  height: 12px;
  background-image: url("../../images/icons/svg-icons.svg#chevron-blue");
  background-repeat: no-repeat;
  background-size: 20px 15px;
  background-position: center;
  margin-left: 7px;
  transform: rotateX(180deg);
}

main .toctree-wrapper > ul .toctree-l1.collapsible.collapsed ul {
  position: relative;
}

main .toctree-wrapper > ul .toctree-l1.collapsible.collapsed .toggle::after {
  transform: rotateX(0);
}

main .toctree-wrapper > ul .toctree-l1.collapsible.collapsed .toctree-l2:nth-of-type(n+4) {
  display: none;
}

main .toctree-wrapper > ul .toctree-l1 a {
  border-left: 3px solid #256bd1;
  position: relative;
  left: -3px;
  padding-left: 15px;
  display: inline-block;
  color: #000;
  line-height: 32px;
  font-weight: 400;
}

main .toctree-wrapper > ul .toctree-l1 a:hover {
  color: #3585f9;
  text-decoration: none;
}

main .toctree-wrapper > ul .toctree-l1 > a {
  font-size: 22px;
  line-height: 22px;
  font-weight: 700;
  padding-bottom: 15px;
}

main .toctree-wrapper > ul .toctree-l2 {
  position: relative;
}

.loading .toctree-l1 {
  height: 133px;
  overflow-y: hidden;
}

#header {
  height: 215px;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#header .navbar-brand {
  font-size: 22px;
}

#header .navbar-brand .logo-link.web {
  padding: 0;
  margin-right: 10px;
}

#header .navbar-brand .logo-link.docs {
