}

#right-sidebar {
  position: fixed;
  top: 215px;
  right: 0;
  width: calc((100% - 1140px)/2);
  min-width: 150px;
  padding-top: 15px;
}

.index footer {
  position: static;
  padding-left: 0;
  padding-right: 0;
}

